  created() {
    if(this.user.token){
      setup()
    }
  },
  mounted() {
  },
  methods: {
    ...mapMutations({
      updateMsgList:'updateMsgList',
      updateRoomList: 'updateRoomList'
    })
  },
  watch: {},
  computed: {
    ...mapGetters(["msgList"]),
    ...mapState([
      'user'
    ])
  
  },
